import React from 'react';
import {Route, Redirect} from 'react-router-dom';
import {MyContext} from '../../context/context';

export const ProtectedRoute = ({component : Component, ...rest}) => {
	return(
		<Route 
		{...rest} 
		render={props => {
			
			return (
				 <MyContext.Consumer>
          {(context) => (context.state.isLoggedIn ? <Component {...props} /> : <Redirect to={{pathname : '/login', state : props.location}} />)}
        </MyContext.Consumer>


				)
			}
		}/>

		);
}; 