import React, { useState }  from 'react';
import './filters.css';
import '../../../static/Grid.css';
import Button from '@material-ui/core/Button';
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import LazyLoad from 'react-lazy-load';
import Divider from '@material-ui/core/Divider';
import { withStyles } from '@material-ui/core/styles';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Slider from '@material-ui/core/slider';
import {MyFilterContext} from '../../context/filtersContext';
import { useMediaQuery } from 'react-responsive';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';


const marks = [
  {
    value: 0,
    label: 'All',
  },
  {
    value: 20,
    label: '₹ 60',
  },
  {
    value: 40,
    label: '₹ 120',
  },
  {
    value: 60,
    label: '₹ 180',
  },
  {
    value: 80,
    label: '₹ 240',
  },
   {
    value: 100,
    label: '₹ 300',
  },
];





const useStyles_filters = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(3),
      padding: theme.spacing(3),
      width : '100%',
      height: 400,
      float : 'right',
    },
  },
  slider : {
  		 display: 'flex',
    '& > *': {
     	color: '#FF4949',
    },
  }
}));

const GreenCheckbox = withStyles({
  root: {
    color: '#FF4949',
    '&$checked': {
      color: '#FF4949',
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);




function FilterView(props){
	const classes = useStyles_filters();
	 const [count, setCount] = useState(0);
	 const [maxPrice, setMaxPrice] = useState(0);
	 const [sendPrice, setSendPrice] = useState(0);
	 const [reset, toggleReset] = useState(false);
	 const [checkBoxList,setCheckBoxList] = useState({
	 	'Royal Enfield' : false,
	 	'Honda' : false,
	 	'Tvs' : false,
	 	'Bajaj': false,
	 	'all' : true
	 });
	 const [brandList, setBrandList] = useState([]);
	 const { state, filterResetToggle, filterSetBrand,filterSetMaxPrice } = React.useContext(MyFilterContext);


    const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
    });
    const isBigScreen = useMediaQuery({ query: '(min-device-width: 1824px)' });
    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' });
    const isTabletOrMobileDevice = useMediaQuery({
      query: '(max-device-width: 1224px)'
    });







    const generateBrandList = () => {
    	let checkboxes = document.getElementsByName('FilterBrand');
    	let BrandList = [];
      for(var i=0, n=checkboxes.length;i<n;i++) {
        if(checkboxes[i].checked && BrandList.includes(checkboxes[i].value) === false ) {
        	BrandList.push(checkboxes[i].value);

        }
      }
      if(BrandList.length==0){
      	BrandList = ['all'];
      }
      return BrandList;

    }

    const sendFilterData = () => {
    	filterResetToggle(reset);
    	
    	
    	const BrandList  = generateBrandList();
    	filterSetBrand(BrandList);
    	

    }

    const sendPriceData =() =>{
    	filterSetMaxPrice(sendPrice);
    	filterResetToggle(reset);
    	


    }



    /*const toggleAll = (event) => {
     
        setCheckBoxList({
		'Royal Enfield' : true,
	 	'Honda' : true,
	 	'Tvs' :  true,
	 	'Bajaj':  true,
	 	'all' : true
				})
		sendFilterData();
			
        
   
    }*/

   /* const toggleAllOff = (event) => {
     
        setCheckBoxList({
		'Royal Enfield' : false,
	 	'Honda' :  false,
	 	'Tvs' :  false,
	 	'Bajaj':  false,
	 	'all' : false
				});
        console.log("inside toggle",checkBoxList);
   
    }*/

	const handleChange = (event) => {
			setCheckBoxList({
				...checkBoxList,
				[event.target.value] : event.target.checked

				});
			sendFilterData();
			
			
	}

	const handlePriceChange= (event, value) => {
		switch(value){
			case 0:
				setMaxPrice(0);
				setSendPrice(0);
				 break;
			case 20:
				setMaxPrice(20);
				setSendPrice(60);
				 break;
			case 40:
				setMaxPrice(40);
				setSendPrice(120);
				 break;
			case 60:
				setMaxPrice(60);
				setSendPrice(180);
				 break;
			case 80:
				setMaxPrice(80);
				setSendPrice(240);
				 break;
			case 100:
				setMaxPrice(100);
				setSendPrice(300);
				 break;


		}
		sendPriceData();
		
		
	}


    return(<div>
    {!isDesktopOrLaptop && isTabletOrMobile ?(
      <div className='filterMobEx'>
            <ExpansionPanel >
                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    
                  >
                  <div className='OptionName'>Brands </div>
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails>
                           <div className='TitleOptions'>
                           
                           <FormGroup row className='no-gutters'>
                           
                         {/*   <FormControlLabel
                           control={<GreenCheckbox type='checkbox' id='All' name="All" value='All' onChange= {toggleAll}/>}
                           label="All"
                         />*/}

                            <FormControlLabel
                           control={<GreenCheckbox type='checkbox' id='checkedA' name="FilterBrand" value='Royal Enfield' onChange= {handleChange}  checked={checkBoxList['Royal Enfield']} />}
                           label="Royal Enfield"
                         />
                          <FormControlLabel
                           control={<GreenCheckbox type='checkbox' id='checkedB' name="FilterBrand" value='Honda'  onChange= {handleChange}  checked={checkBoxList['Honda']} />}
                           label="Honda"
                         />
                          <FormControlLabel
                           control={<GreenCheckbox type='checkbox'  id='checkedC' name="FilterBrand" value='Tvs'  onChange= {handleChange}   checked={checkBoxList['Tvs']}/>}
                           label="TVS"
                         />
                          <FormControlLabel
                           control={<GreenCheckbox id='checkedD' name="FilterBrand" value='Bajaj'  onChange= {handleChange} checked={checkBoxList['Bajaj']}/>}
                           label="Bajaj"
                         />
                         </FormGroup>

                           </div>

                  </ExpansionPanelDetails>
                </ExpansionPanel>



                <ExpansionPanel className='filterPriceEx'>
                      <ExpansionPanelSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                      >
                        <div className='OptionName'> Price Range</div>
                      </ExpansionPanelSummary>
                 
                       
                               <div className='PriceSlider'>
                            

                                <Slider
                               aria-labelledby="discrete-slider-custom"
                               step={20}
                               track={false}
                               marks={marks}
                               className={classes.slider}
                               onChangeCommitted ={handlePriceChange}
                               onChange = {handlePriceChange}
                               value = {maxPrice}
                             />
                                </div>
                    
                    </ExpansionPanel>

            </div>

      ) 
    :
     (

         <div className='filterRoot no-gutters'>
         <div className={classes.root + " filterMobCont"}>
         <Paper variant="outlined">
         <div className='filter'>

         <div className='filterHead'>
         <center>Filters</center>
          <Divider variant="middle" />
         </div>
        

         <div className='filterBody'>
         <div className='TitleOptions'>
                     <div className='OptionName'>Brands </div>
                     <FormGroup row className='no-gutters'>
                     
                   {/*   <FormControlLabel
                     control={<GreenCheckbox type='checkbox' id='All' name="All" value='All' onChange= {toggleAll}/>}
                     label="All"
                   />*/}

                      <FormControlLabel
                     control={<GreenCheckbox type='checkbox' id='checkedA' name="FilterBrand" value='Royal Enfield' onChange= {handleChange}  checked={checkBoxList['Royal Enfield']} />}
                     label="Royal Enfield"
                   />
                    <FormControlLabel
                     control={<GreenCheckbox type='checkbox' id='checkedB' name="FilterBrand" value='Honda'  onChange= {handleChange}  checked={checkBoxList['Honda']} />}
                     label="Honda"
                   />
                    <FormControlLabel
                     control={<GreenCheckbox type='checkbox'  id='checkedC' name="FilterBrand" value='Tvs'  onChange= {handleChange}   checked={checkBoxList['Tvs']}/>}
                     label="TVS"
                   />
                    <FormControlLabel
                     control={<GreenCheckbox id='checkedD' name="FilterBrand" value='Bajaj'  onChange= {handleChange} checked={checkBoxList['Bajaj']}/>}
                     label="Bajaj"
                   />
                   </FormGroup>

                     </div>

           <Divider/>

            <div className='PriceSlider'>
                         <div className='OptionName'> Price Range</div>

                          <Slider
                         aria-labelledby="discrete-slider-custom"
                         step={20}
                         track={false}
                         marks={marks}
                         className={classes.slider}
                         onChangeCommitted ={handlePriceChange}
                         onChange = {handlePriceChange}
                         value = {maxPrice}
                       />
                          </div>
          



         </div>


         </div>
         </Paper>
         </div>
         </div>

        ) }	
        </div>
		)

}

function Filters(props) {

  const isDesktopOrLaptop = useMediaQuery({
    query: '(min-device-width: 1224px)'
  });
  const isBigScreen = useMediaQuery({ query: '(min-device-width: 1824px)' });
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1068px)' });
  const isTabletOrMobileDevice = useMediaQuery({
    query: '(max-device-width: 1224px)'
  });


			
			return(<React.Fragment>
					<FilterView />
					   </React.Fragment>)
}

export default Filters;