import React from 'react';

const MyFilterContext = React.createContext();

// Then create a provider Component
class MyFilterProvider extends React.Component {
  state = {
  filterReset : true,
  filterMaxPrice: 0,
  filterBrand : ['all']
  }
  //this.setState({  newsrc: this.state.newsrc.concat(src) })
  render() {
    return (
      <MyFilterContext.Provider value = {{
      	state: this.state,
        filterResetToggle: () => {
          this.setState({ 
          filterReset : !this.state.filterReset
           });
        },
        
        filterSetMaxPrice: (maxPrice) => {this.setState({ filterMaxPrice : maxPrice })},
        filterSetBrand: (...BrandList) => {this.setState({ filterBrand :BrandList })}
      }}>
        {this.props.children}
      </MyFilterContext.Provider>
    )
  }
}

export {MyFilterContext};

export default MyFilterProvider;