import React, { Component, useState, setState, useContext } from 'react';
import Dialog from '@material-ui/core/Dialog';
import TextField from '@material-ui/core/TextField';
import './login.css';
import { useHistory } from "react-router-dom";
import axios from 'axios';
import stringify from 'qs-stringify';
import ls from 'local-storage';
import { Redirect } from 'react-router';
import {MyContext} from '../../context/context';
import CloseIcon from '@material-ui/icons/Close';


function GuestUser(props) {
   const [open, setOpen] = React.useState(true);
  const [eop, seteop] =  React.useState('');
  const [pass, setpass] =  React.useState('');
   const { state, login } = React.useContext(MyContext);

 let history = useHistory();
  const handleClose = () => {
    setOpen(false);
    history.push('/');
}

   const handleChange = (event) => {
  const value = event.target.value;
  if(event.target.name ==='eop'){
  seteop(value);
}
else {
  setpass(value);
}
} 


function handleSubmit(event) {
         event.preventDefault();
        

 axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
      stringify({ login:'',
                  eop : eop,
                  pass : pass }))
    .then((response) =>{
      console.log(response.data);
      if(response.data.success){

        /* After Validation Code START */
        console.log(response.data);
         ls.set('username', response.data.username);
         ls.set( 'uniqueid', response.data.uniqueid);
         ls.set( 'ssto' ,response.data.ssto);
        ls.set('t2' , response.data.t2);
        ls.set('tokenId' , response.data.tokenId);
         ls.set('isLoggedIn' , true);
               
        console.log(ls.get('username'));
        console.log(ls.get('tokenId'));
           login();
          history.push("/"); 


         }
          else {
        console.log("user not logged in");
          /* After Validation Code END */
        
      }
      

       });

}

  return(
     <Dialog
        open={open}
        onClose={handleClose}
        fullScreen
      >
      <div className='close' onClick={handleClose}><CloseIcon fontSize='large'/> </div>
      <div className='container'>
      <div className='LoginTitle'>
     <center> Sign In </center></div>
         <div className='LoginBody'>
           <form autoComplete="on" onSubmit = {handleSubmit} method='post'>
      <div className='EOP'><TextField  name='eop' label="EOP" variant="outlined" type='text' fullWidth value={eop}  onChange={handleChange} /></div>
       <div className='PASS'><TextField  name='pass' label="Password" variant="outlined" type='Password' fullWidth value={pass}  onChange={handleChange} /></div>
       <center>
          <button type='submit' className='btn-login'>
            Login
          </button>
          </center>
    </form>
        
          </div>
          </div>
      </Dialog>

    )
}





function Login(props) {
    const [isLoggedIn, setisLoggedIn] = React.useState(ls.get('isLoggedIn'));

  console.log(isLoggedIn);

  return (!isLoggedIn ? <GuestUser/> : <Redirect to='/'/>)
}
export default Login;