import React, { Component } from "react";
import './NavBar.css';
import '../../../static/Grid.css';
import {Switch, Route, Link, useLocation,useHistory} from "react-router-dom";
import ls from 'local-storage';
import {MyContext} from '../../context/context';
import Login from '../login/login';
import MenuIcon from '@material-ui/icons/Menu';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import DashboardIcon from '@material-ui/icons/Dashboard';
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import StorageIcon from '@material-ui/icons/Storage';
import HomeIcon from '@material-ui/icons/Home';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import { useMediaQuery } from 'react-responsive';



function DashboardBtnGroup() {
   return(    <div className='nav-links-box'>
         <Link
            to={{pathname: "/Upload"}}
            className="nav-link"
          >
          Upload
          </Link>

          <Link
            to={{pathname:'/home'}}
            className="nav-link"
          >
          Home
          </Link>

        
        
        </div>)
}


function LoginSignUpButtons(){
  return(    <div className='nav-links-box'>
       <Link
            to={{pathname: "/Login"}}
            className="nav-link"
          >
          Login
          </Link>

          <Link
            to={{pathname:'/Signup'}}
            className="nav-link"
          >
          signUp
          </Link>
        </div>)
}

function DashBoardLogoutButtons(){
  return(    <div className='nav-links-box'>
         
       <Link
            to={{pathname: "/Logout"}}
            className="nav-link"
          >
          Logout
          </Link>

          <Link
            to={{pathname:'/Dashboard'}}
            className="nav-link"
          >
          Dashboard
          </Link>

        
        
        </div>)
}

function NavBarBtnGroup(){
  let location = useLocation();
  console.log(location);
  if(location.pathname==='/Dashboard') {
    return(<DashboardBtnGroup/>)
  }
  else{
    return(<MyContext.Consumer>
          {(context) => (context.state.isLoggedIn ? <DashBoardLogoutButtons/> : <LoginSignUpButtons/>)}
        </MyContext.Consumer>)
  }
}

function NavDesktop() {
  return(
    <div className='Nav row no-gutters'> 
         
         <div className='col-6'>
         <div className='logo'>
             IntWiff
      </div>
      </div>
      <div className='col-6'>
       <NavBarBtnGroup/>
       
        </div>
    
</div>
    )
}

function NavMob() {
 
  const [state, setState] = React.useState({
    left: false,
  });

  const anchor = 'left';

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };


  return(
    <div className='Nav row no-gutters'> 
          <div className='HamBurger' onClick={toggleDrawer(anchor, true)}><MenuIcon fontSize= 'large'/></div>
         <div className='logo'>
             IntWiff
      </div>

          <SwipeableDrawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)} className='AppDrawer'>
         <div className='backBtn' onClick={toggleDrawer(anchor, false)}><ArrowBackIcon fontSize= 'large'/></div>
        <div className='labelLogin'>Login or SignUp </div>
         <NavBarBtnGroup/>
        
          </SwipeableDrawer>
       
       
       
       
    
</div>
    )
}


function BottomNav() {
  const history = useHistory();
  const [value, setValue] = React.useState(0);
   const isDesktopOrLaptop = useMediaQuery({
    query: '(min-device-width: 1224px)'
  });
  const isBigScreen = useMediaQuery({ query: '(min-device-width: 1824px)' });
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' });
  const isTabletOrMobileDevice = useMediaQuery({
    query: '(max-device-width: 1224px)'
  });


  return (<div>
    <MyContext.Consumer>

      {(context)=> (isTabletOrMobile || isTabletOrMobileDevice ? (<BottomNavigation
      value={value}
      onChange={(event, newValue) => {
        setValue(newValue);
        history.push(('/' + newValue));
      }}
      showLabels
      className="BottomNav"
    >
     <BottomNavigationAction value="home" label="Home" icon={<HomeIcon />} />
      <BottomNavigationAction value={context.state.isLoggedIn ? "Upload" : "Login"} label={context.state.isLoggedIn ? "Upload" : "Login"} icon={context.state.isLoggedIn ? <AddAPhotoIcon /> : <AccountCircleIcon />} />
              <BottomNavigationAction  value={context.state.isLoggedIn ? "Dashboard" : "SignUp"} label={context.state.isLoggedIn ? "Dashboard" : "Register"} icon={context.state.isLoggedIn ? <DashboardIcon /> : <PersonAddIcon />} />
     
     
    </BottomNavigation>) : null)}
       </MyContext.Consumer>
      </div>
  );
}

 function NavBar(){


  const history = useHistory();
  const [value, setValue] = React.useState(0);
   const isDesktopOrLaptop = useMediaQuery({
    query: '(min-device-width: 1224px)'
  });
  const isBigScreen = useMediaQuery({ query: '(min-device-width: 1824px)' });
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 900px)' });
  const isTabletOrMobileDevice = useMediaQuery({
    query: '(max-device-width: 900px)'
  });




	return(

      <div>
      {isTabletOrMobile || isTabletOrMobileDevice ? <NavMob/> : <NavDesktop/> }
      </div>

		)

}

export {BottomNav};
export default NavBar;
