import React, { Component } from 'react';
import Dialog from '@material-ui/core/Dialog';
import TextField from '@material-ui/core/TextField';
import { useHistory } from "react-router-dom";
import './signup.css';
import axios from 'axios';
import stringify from 'qs-stringify';
import Loader from 'react-loader-spinner';
import Alert from '@material-ui/lab/Alert';
import CloseIcon from '@material-ui/icons/Close';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';

 
function OtpVerify(props){
	return(
		          <div className='SignUpCont'>
                    <div className='signupheader'> {!props.otps ? "Enter Mobile" : "Enter OTP"}</div>
                           <form onSubmit = {props.hs}>
                  { !props.otps ?  <div className='Mobile'> <TextField id="phone" label="Mobile" variant="outlined" fullWidth onChange={props.hc} type='number' /></div> : <div className='OTP'> <TextField id="otp" label="OTP" variant="outlined" fullWidth type='number' onChange={props.hc} /></div> }  
                    
                      <center>
                         <button type='submit' className='btn-login'>
                          { !props.otps ? "Send OTP" : "Verify OTP" }
                         </button>
                          { props.load ?  <Loader type="ThreeDots" color="#FF4949" height="50" width="50" /> : null }

                         </center>
                   </form>
                   

                     </div>
		)
}

















class SignUpForm extends Component {
    
 constructor(props) {
    super(props);
    this.state = {
    			  otptoken:props.otptoken,
    			  phone : props.phone,
    			  name : '',
    			  email : '',
    			  pass : '',
    			  loading: false,
    			  error : '',
            State : [],
            City : [],
            AddressLines : [],
            selectedState : '',
            selectedCity : '',
            selectedAL : ''


    			};
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getCity = this.getCity.bind(this);
     this.getAdrressLines = this.getAdrressLines.bind(this);
     this.handleChangeTxt = this.handleChangeTxt.bind(this);
    
}

componentDidMount(){
  this.getData();
  
}


getData() {
  axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
      stringify({ fetchPlaces :'',
                  
    }))
      .then((response) => {
               this.setState({data : response.data});
               
                 let state = [];
               for(let i=0; i<response.data.length; i++) {
                   
                    this.setState({'State' : state.concat(response.data[i].state)});
                    

               }


  })
}





getCity(){
        console.log(this.state.selectedState);
        for(let i=0; i<this.state.data.length; i++) {
                    let state = this.state.selectedState;
                   if(this.state.data[i].state === state) {
                    let city = [];

                     for(let j=0; j<this.state.data[i].cities.length; j++) {
                   console.log(j,"<" ,this.state.data[i].cities.length);
                    
              city.push(this.state.data[i].cities[j].city);
              this.setState({'City' : city});
                  
                    
               

               }
               console.log(city);
             }


               }
      
     
}


getAdrressLines(){

       for(let i=0; i<this.state.data.length; i++) {
                    let state = this.state.selectedState;
                   if(this.state.data[i].state === state) {
                     for(let j=0; j<this.state.data[i].cities.length; j++) {
                   
                    let city = this.state.selectedCity;
                     
                   
                    let address = [];
                     if(this.state.data[i].cities[j].city === city) {
                        
                        this.setState({'AddressLines' : address.concat(this.state.data[i].cities[j].address)});
                        console.log(this.state.data[i].cities[j].address);
                  }
               

               }
             }


               }
      
      
}

onSelected(event){
    if(event ==='selectedCity') {
      console.log("onselect sc running....");
     this.getAdrressLines();
    }
    if(event ==='selectedState') {
      console.log("onselect ss running....");
    this.getCity.bind(this);

    }
}


handleChange(event) {
            this.setState({ [event.target.name] : event.target.value}, () => {
              const value = event.target.name;
              if(value==='selectedState') {this.getCity()}
              if(value==='selectedCity') {this.getAdrressLines()}

    
});
          
          
           
             
}

handleChangeTxt(event) {
  this.setState({ [event.target.name] : event.target.value});

}

handleSubmit(event) {
        event.preventDefault();
    this.setState({'loading':true});

    axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ signup:'',
    				email :this.state.email,
    				name : this.state.name,
    				pass : this.state.pass,
    				phone : this.state.phone,
    				otk : this.state.otptoken,
            state : this.state.selectedState,
            city :  this.state.selectedCity,
            address :  this.state.selectedAL
    		        


    }))
    .then((response) =>{
    	console.log(response.data);
    	this.setState({'loading':false});
    	if(response.data.success){
    		console.log("user created");
        /* ----------------------After Validation Code---START-----------*/
        this.props.history.push('/login');
         /* ----------------------After Validation Code END--------------*/
    	}
    	else {
    		console.log("user not created");
    		
    	}

    });

}


    render(){
    	return(
    		<div className='signupform'>
    		<div className='Form-header'>Complete Your SignUp </div>
    		<form onSubmit={this.handleSubmit}>
    		 <div className='Name'> <TextField required name='name' label="Name" variant="outlined" fullWidth onChange={this.handleChangeTxt} type='text' value={this.state.name} /></div>
    		 <div className='Email'> <TextField required name='email' label="Email" variant="outlined" fullWidth onChange={this.handleChangeTxt} type='email' value={this.state.email} /></div>
       
         <FormControl required fullWidth className='SelectForm'>
        <InputLabel id="stateSelect">State</InputLabel>
        <Select labelId="stateSelect" name='selectedState'  onChange={this.handleChange}>
       
       {this.state.State.map(function(st){
              
              return(<MenuItem value={st}> {st} </MenuItem>)
        })}
      
        </Select>
        </FormControl>
        <FormControl required fullWidth className='SelectForm'>
        <InputLabel id="citySelect">City</InputLabel>
        <Select  labelId="citySelect" name='selectedCity'  onChange = {this.handleChange}> 
        

            {this.state.City.map(function(st){
              return(<MenuItem value={st}> {st} </MenuItem>)
        })}

         </Select>
          </FormControl>
         <FormControl  margin='normal' required fullWidth className='SelectForm'>
         <InputLabel id="alSelect"> Address</InputLabel>
        <Select labelId="alSelect" name='selectedAL' onClick={this.getAdrressLines}  onChange = {this.handleChange}>
        
        {this.state.AddressLines.map(function(st){
              console.log("AL");
              return(<MenuItem value={st}> {st} </MenuItem>)
        })}
        </Select>
        </FormControl>
         <div className='Pass'> <TextField required name='pass' label="Password" variant="outlined" fullWidth onChange={this.handleChangeTxt} type='password' value={this.state.pass}  /></div>
            <button type='submit' className='btn-login'>Register</button>
             </form>
              
    		</div>
    		)
    }



}





class NameForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {phone: '',
    			  otp : '',
    			  otptoken: null,
    			  'otpsent':false,
    			  open : true,
    			  loading: false,
    			  error : '',
    			  otptokenset : false,
            OTPSentNotification : false,
            wrongOTPNotification : false
    			};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleClose =  this.handleClose.bind(this);
	
  }

  handleChange(event) {
    //unrender Notifications whenever form changes
  	if(!this.state.otpsent){
    this.setState({phone: event.target.value});

}
else {
	this.setState({otp:event.target.value});
  this.setState({'OTPSentNotification': false});
  this.setState({'wrongOTPNotification': false});
}
  }


  handleSubmit(event) {
   // alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
    this.setState({'loading':true});

    if(!this.state.otpsent) {
    axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ sendOtp:'',
    		        'phone':this.state.phone


    }))
    .then((response) =>{
      this.setState({'loading':false});
      if(response.data.success){
        this.setState({'otpsent':response.data.OTP_SENT});
        this.setState({'OTPSentNotification': true});
        console.log(response.data);
    }
    else {
        this.setState({'error' : response.data.error});
        this.setState({'OTPSentNotification': false});
    }
    });

}

else {
           axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ 'otpVerify':'',
    		        'otp':this.state.otp,
    		        'phone' : this.state.phone


    }))
    .then((response) =>{
            this.setState({'loading':false});
            if(response.data.success){
              this.setState({'otptoken':response.data.otpToken});
              this.setState({'otptokenset':true});
              console.log("otp token recieved");
              console.log(response.data.otpToken);
            }

            else{
              this.setState({'error': response.data.error});
              this.setState({'wrongOTPNotification': true});
            }
          })
  }
}
  handleClose(){
  	this.setState({'open':false});
    this.props.history.goBack();
  	
  }

  render() {

//  
    return (
    
               	<div className='dialog'>
               	 <Dialog
                       open={this.state.open}
                       onClose={this.handleClose}
                       fullWidth
                       fullScreen
                       

                     >
                  <div className='close' onClick={this.handleClose}><CloseIcon fontSize='large'/> </div>
                    <div className='container signupbox'>
                     <div className='row'>
                     <div className='col-xl-6 leftbox'>
                     <div className='info'>
                        <div className='infohead'>INTWIFF </div>
                        <div className='infobody'> Rent Your Ride On The Go</div>
                     </div>
                       <div className='footer'>IntWiff ©2020 </div>
                     </div>
                     <div className='col-xl-6 rightbox'>
                     {this.state.error ? <Alert severity="error" onClose={() => {this.setState({'error': ''})}}>{this.state.error}</Alert> : null}
      {this.state.error && this.state.wrongOTPNotification ? <Alert severity="error" onClose={() => {this.setState({'wrongOTPNotification': false})}}>Wrong OTP</Alert> : null}
      {this.state.otpsent && this.state.OTPSentNotification ? <Alert severity="success" onClose={() => {this.setState({'OTPSentNotification':false})}}>Otp Has been Sent!</Alert> : null}
     { this.state.otptokenset ? <SignUpForm otptoken={this.state.otptoken} phone = {this.state.phone} />  :  <OtpVerify hs={this.handleSubmit} hc={this.handleChange} otps = {this.state.otpsent} load = {this.state.loading} />  }
           


                     </div>


                     </div>
                    </div>


                     </Dialog>
               	</div>





    );
  }
}


export default NameForm;