import React from 'react';
import './DashBoard.css';
import '../../../static/Grid.css';
import Button from '@material-ui/core/Button';
import ls from 'local-storage';
import axios from 'axios';
import stringify from 'qs-stringify';
import UploadItem from '../uploadItem/uploadItem';


function GetUserData(that){
	        let user = {  username : ls.get('username'),
			  uniqueid : ls.get('uniqueid'),
			  ssto : ls.get('ssto'),
	          tokenId: ls.get('tokenId'),
	          t2 : ls.get('t2'),
				}

			 axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
      stringify({ fetchUserDetail:'',
                  tokenId : user.tokenId,
                  t2 : user.t2,
                  username : user.username,
                  uniqueid : user.uniqueid,
                  ssto : user.ssto

                   }))
    .then((response) =>{
     
      if(response.data.success){
       console.log(response.data.user_details);
        /* After Validation Code START */
        that.setState({
        			'name' : response.data.user_details[0].name,
        			'username' : response.data.user_details[0].username,
        			'email' : response.data.user_details[0].email,
        			'address' : response.data.user_details[0].address,
        			'city' : response.data.user_details[0].city,
        			'state' : response.data.user_details[0].state,
        			'phoneno' : response.data.user_details[0].phoneno,

        })
         ls.set('city',that.state.city);
  		ls.set('state',that.state.state);
  		ls.set('address', that.state.address);
 		ls.set('phoneno', that.state.phoneno);
        if(!response.data.user_items.empty)
        	that.setState({'uploads' : response.data.user_items});

           }
          else {
        console.log("user not logged in");
          /* After Validation Code END */
        
          }
  });

}




class DashBoard extends React.Component {

	constructor(props){
		super(props);
		this.state = {
		username : '',
		name : '',
		email : '',
		address : '',
		city : '',
		state : '',
		phoneno : '',
		uploads : {}      
		}
		
		
	}

componentDidMount() {
		GetUserData(this);
}


	render(){

		return(
				<div className='DashBoard'>
                <div className='row'>
                <div className='col-2 sidebar'>
                
                <div className='Profile'>
                <div className='Name'> {this.state.name}</div>
                <div className='Email'> {this.state.email}</div>
                <div className='Address'> {this.state.address}</div>
                <div className='City'> {this.state.city}</div>
                <div className='State'>{this.state.state} </div>
                <div className='phone'> {this.state.phoneno}</div>
                </div>

                <div className='Options'>
                <div className='orders'><Button  color="primary" disableElevation> Your Orders</Button> </div>
                <div className='uploads'><Button  color="primary" disableElevation> Your Uploads</Button> </div>
                <div className='settings'><Button  color="primary" disableElevation> Settings</Button> </div>
                </div>
                 </div>

                
                 <div className='col-10 dashboardbody'>
                  <UploadItem/>
                  <div> 
                </div>
                </div>
				</div>
				</div>
			)
	}
}






export default DashBoard;