import React from 'react';
import axios from 'axios';
import stringify from 'qs-stringify';
import { usePromiseTracker } from "react-promise-tracker";
import Loader from 'react-loader-spinner';
import { trackPromise } from 'react-promise-tracker';
import {MyFilterContext} from '../../context/filtersContext';
const Item = React.lazy(() => import('./Item'));





const LoadingIndicator = props => {
 const { promiseInProgress } = usePromiseTracker();

   return (
     promiseInProgress && 
    <div>
      <div
      style={{
        width: "100%",
        height: "100",
       display: "flex",
        justifyContent: "center",
        alignItems: "center",
        
     }}
   >    
     <Loader type="ThreeDots" color="#FF4949" height="100" width="100" />
      </div>
    </div>
  );  
 }




class ItemList extends React.Component {
  static contextType = MyFilterContext;

   constructor(props) {
    super(props);
    this.state = {
        items: [''],
        resetFilters : true,
        BrandFilter : false,
        PriceFilter : false
       

    }
}
 

   componentDidMount() {
    
  const Filters = this.context; 
  console.log(Filters);
  if(Filters.state.filterBrand.length>0){
    this.setState({'BrandFilter' : true});
  } 
   if(Filters.state.MaxPrice>=60){
    this.setState({'PriceFilter' : true});
  }


   trackPromise(axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ getAll:''}))
    .then((response) => {this.setState({ items : response.data.items })


  }))


}



  render() {
      
       return(  <MyFilterContext.Consumer>
             
          
       {value => (
       	this.state.items.map((item)=>{
       		
       if(item.title!=null){	return(	<div>
       		



         {  (value.state.filterBrand.includes('all') || value.state.filterBrand[0].includes('all') ) && value.state.filterMaxPrice<60
          ? <Item items={item}/> :

           value.state.filterBrand[0].includes(item.brand) && value.state.filterMaxPrice<60
            ? <Item items={item}/> :null
          }

           { value.state.filterBrand.includes('all') || value.state.filterBrand[0].includes('all') && value.state.filterMaxPrice>=60
            ? item.price < value.state.filterMaxPrice
               ? <Item items={item}/> : null

            : value.state.filterBrand[0].includes(item.brand) && value.state.filterMaxPrice>=60
               ? item.price < value.state.filterMaxPrice
                 ? <Item items={item}/> : null

               : null
          }
           
    
       		</div>)
            }

            else {
            	return( <LoadingIndicator/>)
            }
        })
        )
     }

          </MyFilterContext.Consumer>
       	)      

}
}

export default ItemList