import React from 'react';
import ls from 'local-storage';

const MyContext = React.createContext();

// Then create a provider Component
class MyProvider extends React.Component {
  state = {
    username : ls.get('username'),
	uniqueid : ls.get('uniqueid'),
	ssto : ls.get('ssto'),
	tokenId: ls.get('tokenId'),
	t2 : ls.get('t2'),
	isLoggedIn : ls.get('isLoggedIn'),
  city : ls.get('city'),
  state : ls.get('state'),
  address : ls.get('address'),
  phoneno : ls.get('phoneno'),
  }

  render() {
    return (
      <MyContext.Provider value = {{
      	state: this.state,
      	logout: () => {this.setState({ isLoggedIn : false }); ls.set('isLoggedIn',false)},
        login: () => {this.setState({ isLoggedIn : true }); ls.set('isLoggedIn',true)},
      }}>
        {this.props.children}
      </MyContext.Provider>
    )
  }
}

export {MyContext};

export default MyProvider;