import React from 'react';
import './Jumbo.css';
import search from '../../../static/search.png';
function Jumbo() {

	return(

            <div className='Jumbo'>
           <div className='SearchBox'>
          
<center>
<div className='searchdiv'>
    <input id='search' className='search' type='text' placeholder='Search City or Vehicle'/>
           <img src={search} alt='' id='searchlogo'/>
       </div>  
         
</center>

           </div>
           </div>
		)
}

export default Jumbo;