import React from 'react';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import './uploadItem.css';
import Icon from '@material-ui/core/Icon';
import Button from '@material-ui/core/Button';
import Alert from '@material-ui/lab/Alert';
import Dialog from '@material-ui/core/Dialog';
import TextField from '@material-ui/core/TextField';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';



const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});


var resizeImage = function (settings) {
    var file = settings.file;
    var maxSize = settings.maxSize;
    var reader = new FileReader();
    var image = new Image();
    var canvas = document.createElement('canvas');
    var dataURItoBlob = function (dataURI) {
        var bytes = dataURI.split(',')[0].indexOf('base64') >= 0 ?
            atob(dataURI.split(',')[1]) :
            unescape(dataURI.split(',')[1]);
        var mime = dataURI.split(',')[0].split(':')[1].split(';')[0];
        var max = bytes.length;
        var ia = new Uint8Array(max);
        for (var i = 0; i < max; i++)
            ia[i] = bytes.charCodeAt(i);
        return new Blob([ia], { type: mime });
    };
    var resize = function () {
        var width = image.width;
        var height = image.height;
        if (width > height) {
            if (width > maxSize) {
                height *= maxSize / width;
                width = maxSize;
            }
        } else {
            if (height > maxSize) {
                width *= maxSize / height;
                height = maxSize;
            }
        }
        canvas.width = width;
        canvas.height = height;
        canvas.getContext('2d').drawImage(image, 0, 0, width, height);
        var dataUrl = canvas.toDataURL('image/jpeg');
        return dataURItoBlob(dataUrl);
    };
    return new Promise(function (ok, no) {
        if (!file.type.match(/image.*/)) {
            no(new Error("Not an image"));
            return;
        }
        reader.onload = function (readerEvent) {
            image.onload = function () { return ok(resize()); };
            image.src = readerEvent.target.result;
        };
        reader.readAsDataURL(file);
    });
};






class UploadItem extends React.Component {
     constructor(props){
        super(props);
        this.state = {
        title : '',
        brand : '',
        description : '',
        address : '',
        city : '',
        state : '',
        phoneno : '',
        uploads : {},
        src : [],
        newsrc :[],
        FileCounter:0,
        open : false,
        fileLength :0     
        }
        this.handleResize = this.handleResize.bind(this);
         this.cropImage = this.cropImage.bind(this);
         this.handleChange = this.handleChange.bind(this);
         this.handleClose = this.handleClose.bind(this);
         this.modelOpen = this.modelOpen.bind(this);
    }


hasAlreadyUploaded(user){
    //logic for check;
}








  cropImage(){
     if (typeof this.cropper.getCroppedCanvas() === 'undefined') {
      return;
    }
    console.log("Call------------------------------------");
    console.log(this.state.FileCounter);
    console.log(this.state.fileLength);
    if(this.state.FileCounter+1 >= this.state.fileLength){
            console.log("Data Posted to back end");
            alert("data sent");
            this.handleClose();
            
    }

    else {
        console.log("start",this.state.FileCounter);
        const src = this.cropper.getCroppedCanvas().toDataURL()
           this.setState({  newsrc: this.state.newsrc.concat(src) })
            this.setState(prevState => {
             return {FileCounter : prevState.FileCounter + 1} });
        console.log("Updated",this.state.FileCounter);
        console.log("End------------------------------------");
}

  }   

handleResize(){

      let fileList = document.getElementById('input').files;
      this.setState({'fileLength': fileList.length});

    for (let i = 0, numFiles = fileList.length; i < numFiles; i++) {
  const file = fileList[i];

      resizeImage({
    file: file,
    maxSize: 500
}).then((resizedImage)=> {
   /* this.setState({resized : true});*/
    var src = URL.createObjectURL(resizedImage);
    this.setState({
  src: this.state.src.concat(src)
})

}).catch(function (err) {
    console.error(err);
});
  // ...
}
}

modelOpen(){
    this.setState({'open':true});
  }


handleChange(event) {
    
    if(event.target.files.length<5 && event.target.files.length>0){
    this.handleResize();
    this.modelOpen();
}
else {
    this.setState({'error' : "Only 4 uploads allowed"})
}

}
destroy() {
    this.cropper.destroy();
} 

handleClose(){
    this.setState({'open':false});
    this.setState({
            'newsrc':[],
             'src' : [],
             'fileLength' : 0,
             'FileCounter' : 0
            });}  


    render(){
        return(<div>{this.state.error ? <Alert severity="error" onClose={() => {this.setState({'error': ''})}}>{this.state.error}</Alert> : null }
         <label for="input"><Icon className='UploadBtn' style={{ fontSize: 40 }}>add_circle</Icon> <br/>Upload</label><input className='visually-hidden' type="file" id="input" accept="image/*" multiple onChange={this.handleChange}/> 
      
        <div className='dialog'>
                 <Dialog
                       open={this.state.open}
                       onClose={this.handleClose}
                       fullWidth
                       TransitionComponent={Transition}
                       fullScreen
                       
                       >
                       <div className='appbar'>
                       <AppBar>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={this.handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        </div>
                    
                                 

                    <div className='container uploadbox'>
                    <div className='Header'>
                   <center> <div className='headtitle'>Crop Your Photos</div></center>
                    <div className='nextBtndiv'>
                    <div className='nextBtn'>

                   <Button variant="contained" color="primary" disableElevation onClick={this.cropImage}> Next </Button>
            </div>
            </div>
            </div>
                       
      

           
       <Cropper
        ref={cropper => { this.cropper = cropper; }}
        src={this.state.src[this.state.FileCounter]}
        style={{height: '100%', width: '100%'}}
        // Cropper.js options
        aspectRatio={16 / 9}
        dragMode = {"none"}
        guides={false}
        center = {false}
        background ={false}
        movable = {false}
        cropBoxResizable = {false}
        destroy  ={this.destroy = this.destroy.bind(this)}
        />

     
                    </div>
  <center>                  
<div className='thumbnailBox'>

{this.state.newsrc.map((src, index) => {
        return <img className='thumbnail' src={src}/>
      })}

</div>
</center>
                     </Dialog>
                </div>
        </div>)
    }



}

export default UploadItem;







